//  歌单详情  

<template>
  <div class="playList">
    <template v-if="songDetail">
      <div class="goBack">
        <van-icon name="arrow-left" color="#ffffff" size="20" class="icon" @click="go" />
        <span>歌单</span>
      </div>
      <div class="header">
        <div class="mask" :style="{backgroundImage: `url(${songDetail.coverImgUrl})`}"></div>
        <div class="fl">
          <img :src="songDetail.coverImgUrl" />
          <span class="playcount">
            <i class="fa fa-headphones"></i>
            {{formattingNumbers(songDetail.playCount)}}
          </span>
          <span class="icon">歌单</span>
        </div>
        <div class="fr">
          <h3>{{songDetail.name}}</h3>
          <img :src="songDetail.creator.avatarUrl" />
          <span>{{songDetail.creator.nickname}}</span>
        </div>
      </div>
      <div class="info">
        <div class="tags">
          <span>标签:</span>
          <i v-for="(tag,index) in songDetail.creator.expertTags" :key="index">{{tag}}</i>
        </div>
        <div class="desc" :class="{show: showMore}">
          <span>简介：</span>
          <span>{{songDetail.description}}</span>
        </div>
        <div class="more" @click="showMore = !showMore">
          <i v-if="showMore" class="fa fa-angle-up"></i>
          <i v-else class="fa fa-angle-down"></i>
        </div>
      </div>

      <div class="songItem">歌曲列表</div>
      <NewSongList
        v-for="(newSong,index) in songDetail.tracks"
        :key="index"
        :newSongItem="newSong"
        :songInfo="{order : index,isShowInfo:true,newSongItem:newSong}"
        @current-song="$emit('current-song', $event)"
      ></NewSongList>

      <!-- <div class="songItem">精彩评论</div> -->
    </template>
  </div>
</template>

<script>
import NewSongList from "@/components/NewSongList.vue";

export default {
  name: "PlayList",
  data() {
    return {
      songDetail: null,

      showMore: false
    };
  },
  components: { NewSongList },
  methods: {
    go() {
      this.$router.go(-1);
    },

    //转换播放次数单位
    formattingNumbers(n) {
      if (n > 100000000) return (n / 100000000).toFixed(1) + "亿";
      if (n > 10000) return (n / 10000).toFixed(1) + "万";
    },

    getsongDetail() {
      var id = this.$route.query.id;
      this.axios
        .get("/playlist/detail", {
          params: {
            id
          }
        })
        .then(response => {
          this.songDetail = response.data.playlist;
          // console.log(response);

          this.$toast.clear();

          window.localStorage.setItem(
            "sl-" + id,
            JSON.stringify(response.data.playlist)
          );
        })
        .catch(error => {
          error;
          this.$toast.clear();
        });
    }
  },
  created() {
    this.$toast.loading({
      forbidClick: true,
      duration: 0,
      message: "加载中..."
    });
    var id = this.$route.query.id;
    let cachesongDetail = JSON.parse(window.localStorage.getItem("sl-" + id));
    if (cachesongDetail) {
      this.songDetail = cachesongDetail;
      this.$toast.clear();
    } else {
      this.getsongDetail();
    }
  }
};
</script>

